.footing {
  width: 100%;
  min-height: 15rem;
  background: $S_DARK_COLOR;
  position: absolute;
  bottom: -15rem;
  color: $WHITE_COLOR;

  &.hidden {
    display: none;
  }

  .footer-group {
    border-bottom: solid 1px $S_LIGHT_COLOR;
    padding: 0.5rem 1rem;
    display: flex;
    justify-content: flex-start;
    align-items: baseline;

    &.centering {
      justify-content: center;
    }

    .footer-item {
      margin: 0.5rem;
    }
  }

  .site-logo {
    margin-left: 0.5rem;
    font-size: 3rem;

    img {
      height: 3.6rem;
      margin-top: 0.2rem;
    }
  }
}