.music-ui-container {
  z-index: $Z-MOST-FRONT;
  position: fixed;
  top: 0.5rem;
  right: 0.5rem;
}

@include desktop {
  .music-control-ui-toggle {
    display: none;

    + label{
      position: absolute;
      width: 1.5rem;
      height: 1.5em;
      top: 0rem;
      right: 0rem;
      cursor: pointer;
      z-index: $Z-MOST-FRONT + 2;

      &::after {
        content: url("https://fonts.gstatic.com/s/i/materialiconsoutlined/volume_up/v1/24px.svg");
        pointer-events: none;
      }
    }
  }

  .music-control-ui-toggle:checked {
    + label::after {
      content: url("https://fonts.gstatic.com/s/i/materialiconsoutlined/close/v1/24px.svg");
    }

    ~ .music-control-ui {
      display: grid;
      grid-template: 2rem 2rem / 5rem 5rem;
      transform: none;
      background: $WHITE_COLOR;
      box-shadow: 1.5px 1.5px 2px 1.5px #1e1e1e98;

      * {
        opacity: 1;
      }

      .music-enable-ui {
        grid-row: 1;
        grid-column: 1 / 3;
        width: 8rem;
        margin-right: 2rem;

        input[type="checkbox"] {
          display: none;

          + label {
            display: flex;
            justify-content: space-around;
            align-content: center;
            align-items: center;

            &::before {
              content: "BGM";
              height: 2rem;
              font-size: 1.5rem;
            }
            &::after {
              content: url("https://fonts.gstatic.com/s/i/materialiconsoutlined/volume_off/v1/24px.svg");
              height: 2rem;
              font-size: 1.5rem;
            }
          }

          &:checked + label::after {
            content: url("https://fonts.gstatic.com/s/i/materialiconsoutlined/volume_up/v1/24px.svg");
          }
        }
      }

      .music-volume-ui {
        grid-row: 2;
        grid-column: 1 / 3;
        position: relative;

        #music-volume {
          -webkit-appearance: none;
          width: calc(10rem - 8px);
          height: calc(2rem - 8px);
          margin: 4px;
          opacity: 0;
          position: absolute;
          background: none;
          z-index: $Z-MOST-FRONT + 1;

          + label{
            display: block;
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;

            &::after {
              content: "";
              height: 4px;
              width: calc(10rem - 8px);
              display: block;
              margin: calc(1rem - 2px) 4px;
              background: rgb(97, 97, 97);
            }

            .vomule-indicator {
              height: 4px;
              position: absolute;
              margin: calc(1rem - 2px) 4px;
              background: rgb(27, 224, 27);
              width: 145px;
              will-change: width;

              &::after {
                content: "";
                position: absolute;
                right: 0;
                width: 0.5rem;
                height: 0.5rem;
                background: white;
                border-radius: 50%;
                box-shadow: 0px 0px 2px 1px #1e1e2f98;
                transform: translate(0.15rem, -0.15rem);
              }
            }
          }
        }
      }
    }
  }

  .music-control-ui {
    height: 4rem;
    width: 10rem;
    transition: 500ms;
    transform-origin: top right;
    transform: scale(0.1, 0.25);

    * {
      transition: opacity 500ms;
      opacity: 0;
    }
  }
}

@include mobile {
  #ui-toggle {
    display: none;
  }

  .music-control-ui {
    width: 2rem;
    margin-right: 0.5rem;
  }

  .music-enable-ui {
    width: 2rem;
    margin-right: 2rem;

    input[type="checkbox"] {
      display: none;

      + label {
        display: flex;
        justify-content: space-around;
        align-content: center;
        align-items: center;
        &::after {
          content: url("https://fonts.gstatic.com/s/i/materialiconsoutlined/volume_off/v1/24px.svg");
          height: 2rem;
          font-size: 1.5rem;
        }
      }

      &:checked + label::after {
        content: url("https://fonts.gstatic.com/s/i/materialiconsoutlined/volume_up/v1/24px.svg");
      }
    }
  }

  .music-volume-ui {
    display: none;
  }
}
