.reader {
  position: absolute;
  top: 0;
  width: 100vw;
  height: 100vh;
  overflow: hidden;

  .title {
    transition: 800ms;
    position: absolute;
    left: 0;
    height: 60vh;
    text-orientation: upright;
    writing-mode: vertical-rl;
    text-align: center;
    min-width: 4rem;
    margin-left: 1rem;
    z-index: $Z-FLOW;
    background: rgba($WHITE_COLOR, 0.6);

    &.hidden {
      transform: translateX(-120%);
    }

    h1 {
      margin-block-start: 1rem;
      margin-block-end: 1rem;
      line-height: 3rem;
      font-size: 3rem;
    }
  }

  .novel-body {
    font-size: calc(1.2rem * var(--font-size-factor, 1));
    font-family: var(--font-variant, 'sans-serif');
    position: absolute;
    top: 0;
    right: 0;
    width: 30vw;
    height: calc(100vh + 20px);
    writing-mode: vertical-rl;
    background: rgba($WHITE_COLOR, 0.4);
    overflow-x: auto;
    overflow-y: hidden;
    z-index: $Z-FLOW;

    p {
      margin: 0.8rem;
      margin-top: 1rem;
      opacity: 0;
      transition: opacity 800ms;
      height: calc(100vh - 20px);
      min-width: 1.5rem;

      &.shown {
        opacity: 1;
      }
    }
  }

  .scroll-area {
    position: absolute;
    width: 100vw;
    height: 100vh;
    overflow: auto;
    opacity: 0.01;
    z-index: $Z-OVERLAY;

    .scroll-target {
      width: 1rem;
    }
  }
}

.modal {
  position: absolute;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #1212229f;
  top: 0;
  z-index: $Z-MOST-FRONT;

  & &-content {
    &_single {
      display: block;
      padding: 3rem 1rem;
      background: #fefeec;

      &::before {
        content: "!";
        clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
        background: gold;
        width: 1.5rem;
        height: 1.5rem;
        display: inline-block;
        position: relative;
        top: 0.25rem;
        text-align: center;
        font-size: 1.5rem;
      }
    }
  }
}

.settings {
  .container {
    width: 40rem;
    height: 25rem;
    position: absolute;
    top: calc(50vh - 12.5rem);
    left: calc(50vw - 20rem);
    background: white;
    display: flex;
    flex-flow: column nowrap;
  }

  &-open {
    z-index: 8000;
    position: fixed;
    bottom: 0;
    right: 0;
    border: none;
    background: none;

    i {
      transform: none;
      transition: 500ms;

      &:hover {
        transform: rotateZ(180deg);
      }
    }
  }

  .close {
    appearance: none;
    background: none;
    border: none;
    transition: 200ms;
    will-change: color;
    height: 1rem;

    &:hover {
      color: red;
      i {
        background: #1e1e2e28;
      }
    }
    i {
      border-radius: 50%;
      border: solid 2px;
      transition: 200ms;
    }
  }

  .fonts {
    display: flex;
    flex-flow: column nowrap;
    justify-content: space-evenly;
    height: 24rem;

    .font-variants, .size {
      display: flex;
      flex-flow: row nowrap;
      justify-content: space-evenly;
      padding-bottom: 0.2rem;
      border-bottom: 2px solid gray;
      position: relative;

      button {
        appearance: none;
        width: 5rem;
        height: 3rem;
        background: $S_LIGHT_COLOR;
        outline: none;
        border: 1px solid $BLACK_COLOR;
        border-radius: 2px;
        transition: 200ms;
        color: $P_DARK_COLOR;

        &:hover {
          box-shadow: 1px 1px 2px 1px #1e1e2e98;
        }
      }

      &::before {
        position: absolute;
        display: block;
        top: -4rem;
        font-size: 1.5rem;
        border-bottom: 1px solid gray;
        width: 20rem;
        text-align: center;
      }
    }

    .size::before {
      content: "フォントの大きさ";
    }
    .font-variants::before {
      content: "フォント";
    }
  }
}
