@keyframes animated-background-fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes animated-background-fadeout {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
@keyframes animated-background-slide-hide {
  99% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes animated-background-slide-left {
  from {
    transform: translateX(-100%);
    opacity: 1;
  }
  to {
    transform: none;
  }
}
@keyframes animated-background-slide-right {
  from {
    transform: translateX(100%);
    opacity: 1;
  }
  to {
    transform: none;
  }
}
@keyframes animated-background-slide-up {
  from {
    transform: translateY(-100%);
    opacity: 1;
  }
  to {
    transform: none;
  }
}
@keyframes animated-background-slide-down {
  from {
    transform: translateY(100%);
    opacity: 1;
  }
  to {
    transform: none;
  }
}