@import "utils/lengths";
@import "utils/color";

@mixin noselect {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

@mixin mobile {
  @media #{map-get($breakpoints, mobile)} {
    @content;
  }
}

@mixin desktop {
  @media #{map-get($breakpoints, desktop)} {
    @content;
  }
}

@mixin animated-underline($color: #f00, $height: 2px, $max-width: 100%, $left: 0, $bottom: 0, $anim: all 200ms ease) {
  position: relative;

  &::after {
    content: "";
    display: block;
    width: 0;
    height: $height;
    border-radius: 0.5px;
    position: absolute;
    bottom: 0;
    background: $color;
    transition: $anim;
    left: $left;
    bottom: $bottom;
  }
  &:hover::after {
    width: $max-width;
  }
}

:root {
  @include mobile {
    --is-mobile: yes;
  }
  @include desktop {
    --is-mobile: no;
  }
  @media screen and (orientation: landscape) {
    --is-landscape: yes;
  }
  @media screen and (orientation: portrait) {
    --is-landscape: no;
  }
}
