:root {
  @include desktop {
    font-size: 17px;
  }
  @include mobile {
    font-size: 13.6px;
  }
}

body {
  margin: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.App {
  position: absolute;
  top: 0;
  background: $WHITE_COLOR;
  width: calc(100vw - 1rem);
  min-height: 100vh;
}

.hamburger.hidden + .App {
  width: 100vw;
}

.interactable {
  cursor: pointer;
}

main {
  width: 100%;
  min-height: 100vh;
}

span[data-ruby] {
  position: relative;

  &::after {
    content: attr(data-ruby);
    position: absolute;
    writing-mode: vertical-lr;
    height: 100%;
    top: 0;
    font-size: 0.5rem;
    right: -55%;
    letter-spacing: -1px;
    display: block;
    word-break: keep-all;
    text-align-last: justify;
    text-align: justify;
    text-justify: inter-character;
  }
}

.spinner {
  position: absolute;
  width: 5rem;
  height: 5rem;
  bottom: calc(50% - 2.5rem);
  left: calc(50% - 2.5rem);
  border: solid 4px;
  border-bottom: none;
  border-radius: 50%;
  animation: cubic-bezier(0.68, -0.55, 0.27, 1.55) spinner-rotation 1.5s infinite;
  transform: rotate(-135deg);
}

h1, h2 {
  border-bottom: solid 2px $S_LIGHT_COLOR;
  margin: 1rem;
}

@keyframes spinner-rotation {
  0% {
    transform: rotate(-135deg);
  }
  100% {
    transform: rotate(225deg);
  }
}