.heading {
  background: $P_DARK_COLOR;
  height: 4rem;

  &.hidden {
    display: none;
  }

  div {
    float: left;
  }

  .site-logo {
    margin-left: 0.5rem;
    font-size: 3rem;

    img {
      height: 3.6rem;
      margin-top: 0.2rem;
    }
  }
  .site-copy {
    color: $WHITE_COLOR;
    font-size: 0.8rem;
    height: 1rem;
    position: relative;
    top: 2.5rem;
    left: 1rem;
  }
}