.novel-index {
  .novel-details {
    position: relative;

    .novel-author {
      text-align: center;
      margin-bottom: 1rem;

      &::before {
        content: "作者: "
      }
    }
    .novel-summary-container {
      display: flex;

      @include desktop {
        flex-flow: row nowrap;
        margin-left: 5rem;
      }

      @include mobile {
        flex-flow: column nowrap;
      }

      .novel-summary {
        white-space: pre;
        text-align: center;

        @include desktop {
          width: 50vw;
        }
      }
      .novel-thumbnail {
        @include desktop {
          width: 45vw;
        }

        @include mobile {
          width: 100vw;
        }

        img {
          width: 100%;
        }
      }
    }
  }
  .episode-list {
    list-style-type: none;
    margin-bottom: 4rem;

    li {
      display: block;
      margin: 0.5rem;
      margin-top: 1rem;
      margin-right: 1.5rem;

      border-bottom: solid 1px $S_LIGHT_COLOR;

      a {
        margin-left: 1rem;
        color: $S_DARK_COLOR;
        text-decoration: none;
        font-weight: bold;

        &:hover, &:focus {
          color: $PRIMARY_COLOR;
        }
        &:visited {
          color: $SECONDARY_COLOR;
        }
      }
    }
  }
}