@import "animations/animated_background";

.background {
  & &-image {
    position: absolute;
    width: 100vw;
    height: 100vh;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    transform: none;
    animation-duration: inherit!important;
    animation-fill-mode: forwards;

    &.fade {
      &.shown {
        animation-name: animated-background-fadein;
      }
      &:not(.shown) {
        animation-name: animated-background-fadeout;
      }
    }

    &.slide {
      &:not(.shown) {
        animation-name: animated-background-slide-hide;
      }
      &.shown {
        z-index: 1;
        &.left {
          animation-name: animated-background-slide-left;
        }
        &.right {
          animation-name: animated-background-slide-right;
        }
        &.up {
          animation-name: animated-background-slide-up;
        }
        &.down {
          animation-name: animated-background-slide-down;
        }
      }
    }
  }
}
