.contents-container {
  min-height: 100vh;
}

.pickups {
  margin-left: 1rem;

  .book {
    min-height: 13.6vw;
    margin: 2rem;
    margin-left: 0;
    position: relative;

    .image {
      width: 20vw;
      float: left;
      margin-right: 2rem;
    }

    .summary {
      line-height: 1.2rem;
      max-height: 100%;
      margin-left: 2rem;
      padding-top: 2rem;

      &::before {
        content: "あらすじ";
        position: absolute;
        top: 0;
        font-size: 1.5rem;
        height: 1.6rem;
        border-bottom: solid 2px;
      }
    }
    .clicktrap {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
    }
  }
}

.bookshelf {
  .book {
    min-height: 13.6vw;
    margin: 1rem;
    position: relative;
    background: lighten($S_LIGHT_COLOR, 10);
    padding: 2px;

    .detail-container {
      margin-left: 18rem;

      .upper-items {
        height: 2rem;
        display: flex;
        flex-flow: row nowrap;
        margin-bottom: 0.5rem;
        border-bottom: solid 1px $P_DARK_COLOR;

        .creators {
          display: flex;
          align-items: flex-end;
          flex-flow: row nowrap;

          > * {
            margin-left: 1rem;
          }

          .author,
          .graphic-designer,
          .music-director {
            &::before {
              content: "";
              display: inline-block;
              background-repeat: no-repeat;
              background-size: cover;
              height: 1.6rem;
              width: 1.6rem;
              margin-right: 0.3rem;
              margin-bottom: -0.2rem;
            }
          }

          .author::before {
            background-image: url(images/writer.png);
          }
          .music-director::before {
            background-image: url(images/music.png);
          }
          .graphic-designer::before {
            background-image: url(images/graphic.png);
          }
        }

        .title {
          font-size: 1.6rem;
        }
        .type {
          margin: auto 1rem;

          &::before {
            content: "〈";
          }
          &::after {
            content: "〉";
          }
        }
      }
    }

    .image {
      width: 20vw;
      float: left;
    }

    .clicktrap {
      position: absolute;
      top: 0;
      left: 0;
      display: block;
      width: 100%;
      height: 100%;
    }
  }
}