.next-menu {
  width: 20vw;
  height: 100vh;
  position: fixed;
  background: rgba($WHITE_COLOR, 0.4);
  transform: translateX(-110%);
  transition: 500ms;
  z-index: $Z-MOST-FRONT;

  &.shown {
    transform: none;
  }

  .next-menu-title {
    width: calc(50% + 1rem);
    font-size: 2rem;
    writing-mode: vertical-rl;
    margin-top: 2rem;
    @include noselect;
  }

  .episodes {
    display: flex;
    width: 100%;
    flex-flow: row nowrap;
    justify-content: space-evenly;
    font-size: 1.5rem;
    margin-bottom: 4vh;
    padding: 4vh 0;
    position: absolute;
    bottom: 0;

    .disabled {
      pointer-events: none;

      a {
        color: lighten($color: $BLACK_COLOR, $amount: 30);
      }
    }

    a {
      text-decoration: none;
      color: $P_DARK_COLOR;

      @include animated-underline($P_LIGHT_COLOR);
    }
  }
}